import Vue from "vue";
import "_platform/utils";
import "../dsf_config";
import "../assets/styles/designer/designer.scss";

//
import platformForPc from "_platform/pc";
//
import platformForMobile from "_platform/mobile";
//
import flow from "_platform/designer/flow";
//
import designer from "_platform/designer/page/dsfDesigner";
// 
import systemGlobalSetting from "_platform/mixins/systemGlobalSetting";

import store from "../store";

//
import "../metadata";
//api
import "../api";
//pc
import pcComponent from "./../pc/components";
//
import mobileComponent from "./../mobile/components";

import HomeCtrl from "_platform/pc/home/ctrl";

import HomeChart from "_platform/pc/home/chart";

//
import App from "./App.vue";
import router from "./router";

dsf.init.initPlatform(Vue);
dsf.init.globalMixin(Vue);

Vue.use(platformForPc);
Vue.use(platformForMobile);
Vue.use(designer);
Vue.use(flow);
Vue.use(pcComponent);
Vue.use(mobileComponent);
Vue.use(HomeCtrl).use(HomeChart);

var EventBus = new Vue();
//WEBAPI
Object.defineProperties(Vue.prototype, {
  $bus: {
    get: function () {
      return EventBus;
    }
  }
});

router.beforeResolve((to, from, next) => {
  //title
  document.title = to.meta.title || dsf.config.setting_public_logo_title || "";
  next();
});

Vue.config.productionTip = false;
new Vue({
  mixins: [systemGlobalSetting],
  router,
  store,
  data() {
    return {};
  },
  created() {
    //windowresize
    dsf.resizeDetector();
    //IE
    if ("-ms-scroll-limit" in document.documentElement.style && "-ms-ime-align" in document.documentElement.style) {
      //IE
      window.addEventListener(
        "hashchange",
        () => {
          var currentPath = window.location.hash.slice(1);
          if (this.$router.path !== currentPath) {
            this.$router.replace(currentPath);
          }
        },
        false
      );
    }
  },
  render: (h) => h(App)
}).$mount("#app");
