<template>
  <el-container>
    <el-row style="width: 100%">
      <el-col :span="6">
        <el-checkbox v-model="checkAll" border :indeterminate="isIndeterminate" @change="chooseAll"></el-checkbox>
        <ul class="check-list">
          <li v-for="(item, it) in tools" :key="item.val">
            <el-checkbox :label="item.val" :value="item.checked" :checked="false" name="type" @change="chooseItem(item, it)">{{
              item.text
            }}</el-checkbox>
          </li>
        </ul>
      </el-col>
      <el-col :span="18">
        <div class="ue-container">
          <div id="u_template"></div>
        </div>
      </el-col>
    </el-row>
  </el-container>
</template>

<script>
import propertyDialogSetting from "_platform/mixins/propertyDialogSetting";

export default {
  name: "DesUeditorToolbar",
  mixins: [propertyDialogSetting],
  data() {
    return {
      checkAll: false,
      tools: [
        { val: "fullscreen", text: "" },
        { val: "source", text: "" },
        { val: "undo", text: "" },
        { val: "redo", text: "" },
        { val: "bold", text: "" },
        { val: "italic", text: "" },
        { val: "underline", text: "" },
        { val: "fontborder", text: "" },
        { val: "strikethrough", text: "" },
        { val: "superscript", text: "" },
        { val: "subscript", text: "" },
        { val: "removeformat", text: "" },
        { val: "formatmatch", text: "" },
        { val: "autotypeset", text: "" },
        { val: "blockquote", text: "" },
        { val: "pasteplain", text: "" },
        { val: "forecolor", text: "" },
        { val: "backcolor", text: "" },
        { val: "insertorderedlist", text: "" },
        { val: "insertunorderedlist", text: "" },
        { val: "selectall", text: "" },
        { val: "cleardoc", text: "" },
        { val: "rowspacingtop", text: "" },
        { val: "rowspacingbottom", text: "" },
        { val: "lineheight", text: "" },
        { val: "customstyle", text: "" },
        { val: "paragraph", text: "" },
        { val: "fontfamily", text: "" },
        { val: "fontsize", text: "" },
        { val: "directionalityltr", text: "" },
        { val: "directionalityrtl", text: "" },
        { val: "indent", text: "" },
        { val: "justifyleft", text: "" },
        { val: "justifycenter", text: "" },
        { val: "justifyright", text: "" },
        { val: "justifyjustify", text: "" },
        { val: "touppercase", text: "" },
        { val: "tolowercase", text: "" },
        { val: "link", text: "" },
        { val: "unlink", text: "" },
        { val: "anchor", text: "" },
        { val: "imagenone", text: "" },
        { val: "imageleft", text: "" },
        { val: "imageright", text: "" },
        { val: "imagecenter", text: "" },
        { val: "simpleupload", text: "" },
        { val: "insertimage", text: "" },
        { val: "emotion", text: "" },
        { val: "scrawl", text: "" },
        { val: "insertvideo", text: "" },
        { val: "music", text: "" },
        { val: "attachment", text: "" },
        { val: "map", text: "" },
        { val: "gmap", text: "Google" },
        { val: "insertframe", text: "Iframe" },
        { val: "insertcode", text: "" },
        { val: "webapp", text: "" },
        { val: "pagebreak", text: "" },
        { val: "template", text: "" },
        { val: "background", text: "" },
        { val: "horizontal", text: "" },
        { val: "date", text: "" },
        { val: "time", text: "" },
        { val: "spechars", text: "" },
        { val: "snapscreen", text: "" },
        { val: "wordimage", text: "" },
        { val: "inserttable", text: "" },
        { val: "deletetable", text: "" },
        { val: "insertparagraphbeforetable", text: "" },
        { val: "insertrow", text: "" },
        { val: "deleterow", text: "" },
        { val: "insertcol", text: "" },
        { val: "deletecol", text: "" },
        { val: "mergecells", text: "" },
        { val: "mergeright", text: "" },
        { val: "mergedown", text: "" },
        { val: "splittocells", text: "" },
        { val: "splittorows", text: "" },
        { val: "splittocols", text: "" },
        { val: "charts", text: "" },
        { val: "print", text: "" },
        { val: "preview", text: "" },
        { val: "searchreplace", text: "" },
        { val: "drafts", text: "" },
        { val: "help", text: "" },
      ],
      checkedTools: this.owner.toolBar,
      isIndeterminate: false, //
      dialogUe: null,
    };
  },
  watch: {
    checkedTools() {
      renderUEditor.call(this);
    },
  },
  created() {
    const self = this;
    const copy = self.tools.map((item) => {
      item.checked = false;
      return item;
    });
    for (let checked of self.checkedTools) {
      let index = copy.findIndex((item) => item.val === checked);
      copy[index].checked = true;
    }
    self.tools = copy;
    judgeCheckAllStatus.call(self);
  },
  mounted() {
    renderUEditor.call(this);
  },
  methods: {
    chooseItem(item, index) {
      const self = this;
      const current = _.assign({}, item, { checked: !item.checked });
      self.tools.splice(index, 1, current);
      refreshCheckedArr.call(self);
      judgeCheckAllStatus.call(self);
    },
    chooseAll() {
      const self = this;
      self.isIndeterminate = false;
      if (self.checkAll) {
        self.tools.forEach((item) => {
          item.checked = true;
        });
      } else {
        self.tools.forEach((item) => {
          item.checked = false;
        });
      }

      refreshCheckedArr.call(self);
    },
    yes() {
      this.dialogUe.destroy();
      return this.checkedTools;
    },
  },
};

function refreshCheckedArr() {
  const self = this;
  const arr = [];
  for (let item of self.tools) {
    if (!item.checked) continue;
    arr.push(item.val);
  }
  self.checkedTools = arr;
}

function judgeCheckAllStatus() {
  const self = this;
  let checkAll = !self.tools.find((item) => !item.checked);
  if (checkAll) {
    self.checkAll = true;
    self.isIndeterminate = false;
    return;
  }

  let checksome = self.tools.findIndex((item) => item.checked);
  if (checksome !== -1) {
    self.checkAll = false;
    self.isIndeterminate = true;
    return;
  }

  self.checkAll = false;
  self.isIndeterminate = false;
}

function renderUEditor() {
  const self = this;
  if (self.dialogUe) self.dialogUe.destroy();
  self.dialogUe = UE.getEditor("u_template", {
    autoHeightEnabled: false,
    toolbars: [self.checkedTools],
  });
  self.dialogUe.ready(() => {
    initDialogUEditor.call(self);
  });
}

function initDialogUEditor() {
  const self = this;
  $("#u_template").css("width", "100%");
  self.dialogUe.setHeight(390);
}
</script>

<style type="text/scss" lang="scss" scoped>
.check-list {
  margin-top: 15px;
  height: 420px;
  width: 100%;
  overflow-y: scroll;
  box-sizing: border-box;
  padding-right: 5px;
  li {
    padding-top: 5px;
    width: 100%;
  }
  li + li {
    margin-top: 5px;
    border-top: 1px solid #eeeeee;
  }
}

.ue-container {
  width: 98%;
  padding-left: 2%;
  box-sizing: border-box;
}
</style>
